.custom-select {
  position: relative;
  height: 100%;
  width: 100%;
}
.custom-select select {
  display: none; /*hide original SELECT element: */
}

.select-selected {
  cursor: pointer;
  height: 100%;
  position: absolute;
  z-index: 3;
  width: 100%;
  background-color: var(--color-white);
  border-radius: 100px;
  padding: 0 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @include font-d-6;
  color: var(--color-blue-dark);
  border: 1px solid var(--color-blue-light2);
  @include l
  {
    @include font-t-5;
  }
  @include xm
  {
    padding: 0 20px;
    @include font-m-5;
  }
}
.custom-select__arrow
{
  width: 24px;
  height: 24px;
  svg
  {
    width: 100%;
    height: 100%;
  }
  @include xm
  {
    width: 16px;
    height: 16px;
  }
}
/* Style the arrow inside the select element: */
/* Point the arrow upwards when the select box is open (active): */
.select-selected.select-arrow-active {
  .custom-select__arrow
  {
    svg
    {
      transform: rotateZ(180deg)
    }
  }
}

/* style the items (options), including the selected item: */
.select-items div {
  padding: 0 30px;
  margin-top: 20px;
  cursor: pointer;
  @include font-d-6;
  &:last-child
  {
    margin-bottom: 20px;
  }
  @include l
  {
    @include font-t-5;
  }
  @include xm
  {
    padding: 0 20px;
    @include font-m-5;
  }
}

/* Style items (options): */
.select-items {
  position: absolute;
  background-color: var(--color-white);
  border: 1px solid var(--color-blue-light2);
  height: 310px;
  top: 25px;
  left: 0;
  right: 0;
  padding-top: 35px;
  z-index: 2;
  border-radius: 0px 0px 30px 30px;
  overflow-y: scroll;
  @include xm
  {
    padding-top: 0px;
  }
}
.select-items::-webkit-scrollbar {
  width: 5px;
}
.select-items::-webkit-scrollbar-thumb {
  background: linear-gradient(to top, rgba(2,3,4,0) 15%, var(--color-blue2) 15%, var(--color-blue2) 100%);
  height: 25px;
  margin-bottom: 25px;
}
/* Hide the items when the select box is closed: */
.select-hide {
  display: none;
}

.select-items div:hover, .same-as-selected {
}



